@use 'variables' as *;

textarea {
  font-size: var(--fs-input, 14px);
  font-family: var(--ff);
}

@mixin input-shared {
  font-size: var(--fs-input, 14px);
  border: 1px solid var(--border-color, #ced4da);
  border-radius: var(--border-radius-input, 0.35rem);
  padding: var(--sp-input-y, 0.375rem) var(--sp-input-x, 0.75rem);
  appearance: none;
}

.miq- {
  &input {
    @include input-shared;

    display: block;
    width: 100%;
    font-weight: 400;
    line-height: 1.5;
    box-sizing: border-box;

    &.form-invalid {
      outline: none;
      border: 2px solid var(--red-500);
      // outline-color: var(--red-500);
    }
  }

  &select {
    @include input-shared;

    width: 100%;
    font-size: var(--fs-input, 14px);
    border: 1px solid var(--border-color, #ced4da);
    border-radius: var(--border-radius-input, 0.35rem);
    // padding: var(--sp-input-y, 0.375rem) var(--sp-input-x, 0.75rem);

    background-color: white;
  }

  &form {
    &-error {
      grid-column-end: span 12;
      font-size: var(--fs-sm);
      color: var(--red-500);
    }
  }
}
