.close-page {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  .close-page-inner {
    position: relative;
    display: flex;
    width: 100vw;
    height: 100vh;
    align-items: center;
    justify-content: center;

    background-color: transparent;

    color: white;
    text-shadow: 1px 1px 2px #000000;

    .close-template-text {
      font-family: var(--ff);
    }
  }

  img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    object-fit: cover;
  }
}
