.miq-view {
  --fs-xl: 1.725rem;
  --fs-lg: 1.5rem;
  --fs-md: 1.25rem;
  --icon-size: 20px;

  .miq-view-text {
    color: var(--gray-600, gray);
    font-size: var(--fs-sm, 0.8rem);
  }
}

.miq-view-header-inner {
  min-height: 50px;
  align-items: center;
  justify-content: space-between;
}

.miq-view-section {
  margin-bottom: var(--sp-4, 2rem);

  .miq-view-header {
    margin-bottom: var(--sp-2, 1rem);

    .miq-view-title {
      font-size: 1.075rem;
      margin-bottom: var(--sp-1, 0.5rem);

      h2 {
        font-size: 1.025rem;
        font-weight: bold;
        font-family: var(--ff);
      }
    }
  }
  .miq-view-header-inner {
    min-height: 0;
  }
}
